* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  text-align: center;
  background-color: #fafafa;
  background-image: url("background.dcab946e.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: initial;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  min-width: 310px;
  opacity: .9;
  border: 2px solid #ecb638;
  border-radius: 8px;
  margin: 40px auto 16px;
  padding: 8px;
  transition: box-shadow .5s linear, scale .5s linear;
}

#search-box:hover {
  box-shadow: 1px 1px rgba(153, 153, 153, .27), 2px 2px rgba(153, 153, 153, .227), 3px 3px rgba(153, 153, 153, .227), 4px 4px rgba(153, 153, 153, .227), 5px 5px rgba(153, 153, 153, .157), 6px 6px rgba(153, 153, 153, .01);
}

.country-list {
  max-width: 320px;
  padding-left: 10px;
  list-style: none;
}

.country-list li {
  align-items: center;
  display: flex;
}

img {
  width: 60px;
  height: 40px;
  object-fit: cover;
  margin-right: 20px;
  display: block;
}

.country-info {
  width: 320px;
  margin: auto;
}

.country-info p {
  text-align: left;
  font-size: 18px;
}

.country-info span {
  font-weight: 700;
}

.country-title {
  align-items: center;
  display: flex;
}

@media screen and (min-width: 480px) {
  #search-box {
    max-width: 500px;
  }

  .country-list {
    max-width: 1200px;
    padding-left: 40px;
  }

  #search-box:hover {
    transform: scale(1.05);
  }

  .country-info {
    width: 800px;
  }
}

